.meals {
    
    width: 90%;
    margin: 2rem auto;
    animation: meals-appear 1s ease-out forwards;
  }
  
.MealsLoading{
  text-align: center;
  color: rgb(0, 0, 0);
}
.MealsError{
  text-align: center;
  color: red;
}

  .meals ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }


  .card_layout{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  
  @keyframes meals-appear {
    from {
      opacity: 0;
      transform: translateY(3rem);
    }
  
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }